import { Location } from '@angular/common';
import { Injectable } from '@angular/core';
import { Http, Response } from "@angular/http";

import { map } from 'rxjs/operators';
import { Observable, of } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class FileService {
  private _fileData: { [key: string]: Response };

  constructor(private _http: Http,
    private _location: Location) { }

  load(file: string): Observable<Response> {
    if (this._fileData && this._fileData[file]) {
      console.log('get old');
      return of(this._fileData[file]);
    }

    return this._http.get(this._location.prepareExternalUrl(file)).pipe(
      map(res => {
        if (!this._fileData) this._fileData = {};
        this._fileData[file] = res;
        return res;
      }));
  }
}
